* {
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-size: 1rem;
}
*, :after, :before {
    box-sizing: inherit;
}
html, body {
    background-color: black;
    margin: 0;
    height: 100%;
    max-width: 100%;
}
html::-webkit-scrollbar {
    display: none;
  }
html {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
.o-scroll {
    overflow: hidden;
}
header {
    display: block;
}
section {
    display: block;
}
div { 
    display: block;
}
.col {
    padding: 0;
}
.c-fixed {
    height: 100%;
    background-size: cover;
    background-position: 50%;
    opacity: .75;
    mix-blend-mode: multiply;
}
@media (min-width: 1000px){
.c-fixed_wrapper {
    height: 100vh;
}}
.c-fixed_wrapper {
    position: relative;
    overflow: hidden;
    background-color: black;
}
.c-fixed, .c-fixed_target {
    position: absolute;
    top: -100vh;
    right: 0;
    left: 0;
}
.c-fixed_target {
    bottom: -100vh;
}
.o-layout {
    overflow: hidden;
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 0;
 }
 .o-layout.overflow {
     overflow: visible;
 }
 .o-layout_item {
    display: inline-block;
    width: 50%;
    vertical-align: top;
    font-size: 1rem;
    padding-left: 0;
}
.o-layout_item.overflow {
    overflow: visible;
}
.o-layout_item#instas {
    position: relative;
    width: 40%;
}
